<script>
import { getProduct } from "../api/page.js";

export default {
  data() {
    return {
      vantList: {
        productid: '',
        loading: false,
        finished: false,
        list: [],
      },
      noticeBar: {
        color: "#1989fa",
        background: "#ecf9ff",
        // text: "请点击模型CT位置模拟您的测试结果",
      },
        // route:{

        // }
    };
  },
  computed: {},
  created() {
    // console.log( this.$route )
     this.vantList.productid = this.$route.params.id;

  },
watch: {
    $route: {
      handler: function(route) {
        // console.log( route )
        this.vantList.productid = this.$route.params.id;
        this.vantList.loading = true;
        this.vantList.finished = false;
        this.onLoad();
      },
      immediate: true
    }
  },

  methods: {
    onLoad() {  console.log( this)
      if(! this.vantList.productid ){
            return;
      }
      getProduct({ id: this.vantList.productid }).then((response) => {
        this.vantList.list = [];

        response.area = [];
        response.unit.map((item) => {
          response.area.push(
            { label: "c" + item, flag: false },
            { label: "t" + item, flag: false }
          );
        });
        this.vantList.list.push(response);

        this.vantList.loading = false;
        this.vantList.finished = true;
      });
    },

    onClick(item, index, subitem, subindex) {
      this.vantList["list"][index]["area"][subindex]["flag"] =
        !this.vantList["list"][index]["area"][subindex]["flag"];
    },
    goPage(pagepath, pagename, params) {
      this.$router.push({ name: pagename, params: params });
    },
    setclass(item, index, subitem, subindex) {
      return [
        "line-" + subitem.label,
        this.vantList["list"][index]["area"][subindex]["flag"] ? "checked" : "",
      ];
    },
    getPageClass() {
      return ["page-" + this.vantList.productid];
    },
  },
};
</script>
